	import {goodsList} from '../../../common/network/network.js'
	
export default {
	name: 'scrollBar',
	props:{
		tab:{
			type:Array,
			default(){
				return []
			}
		}
	},
	data() {
		return {
			currentIndex: 0
		};
	},
	methods: {
		barItemClick(index,nav) {
			console.log(index,nav)
			this.currentIndex = index;
			// 发送loading数据给vuex
			let isloading = true
			
			// 上拉加载id
			let pageid = 0
			
			// 上拉加载隐藏
			let loadmore = false
			
			// ‘没有更多’隐藏
			let nonedata = false
			this.$store.commit('isShowNoneData',nonedata)
			
			// 将首页数据配置传到vuex
			let navhomeOption = {
				loading:isloading,
				homeGoodsType:nav,
				pageid:pageid,
				loadmore:loadmore
			}
			this.$store.commit('setHomeOption',navhomeOption)
			goodsList(nav,pageid)
			.then(res => {
				let listdata = res.data
				if(listdata.length === 0){
					let nonedata = true
					this.$store.commit('isShowNoneData',nonedata)
				}else{
					let nonedata = false
					this.$store.commit('isShowNoneData',nonedata)
				}
				// vuex
				this.$store.commit('setHomeListData',listdata)
				isloading = false
				this.$store.commit('setLoading',isloading)
			})
			.catch(err => {
				console.log(err)
			})
		}
	}
};
